img{
   width: 220px; 
   height: 220px;
   border-style: solid ;
   border-width: 2px; 
   border-color:  rgba(76, 0, 130, 0.829);

}

.hover11 {
   background: #1abc9c;

}

.hover11 img {
	opacity: 1;
	-webkit-transition: .3s ease-in-out;
   transition: .3s ease-in-out;
}
img:hover {
   opacity: .5;
   background: rgba(255, 255,255, 0.3);
   transition: all 2s ease; 
}